import React from 'react'
import {Link} from 'react-router-dom'

class Pitágoras extends React.Component{

    constructor(){
        super()
        this.state = {
            LadoA:'',
            LadoB:'',
            Resultado:''
        }
    }

    AlterarLadoA = event => {
        this.setState({LadoA: event.target.value})
    }
    AlterarLadoB = event => {
        this.setState({LadoB: event.target.value})
    }

    Calcular = () =>{
        let LadoA = this.state.LadoA
        let LadoB = this.state.LadoB

        let A = Number(LadoA)
        let B = Number(LadoB)

        let soma = (A*A)+(B*B)

        let Raiz = Math.sqrt(soma)

        let hipotenusa = Raiz

        if(hipotenusa < 1){
            hipotenusa = `Uma medida não pode ser negativa ou nula, tente novamente`
        }else{
            hipotenusa = `A hipotenusa é ${hipotenusa}`
        }

        this.setState({Resultado: hipotenusa})

    }

    render(){
        return(
            <div>
                <div className='Cálculos'>
                    <main className='Título'>
                        <h2>Fórmulas Disponíveis</h2>
                    </main>
                    <main className='Seleção'>
                        <div>
                            <p><Link to='/Bashkara'>Delta</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Segundo-Grau'>Bashkara</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Pitágoras'>Pitágoras</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Aulas'>Como Fazer</Link></p>
                        </div>
                    </main>
                    <main className='Fórmulator'>
                        <div className='Inputs'>
                            <h3>Insira os Valores</h3>

                            <main>
                                <p>Lado A: <input type="number" 
                                value={this.state.LadoA}
                                onChange={this.AlterarLadoA}
                                /></p>
                            </main>
                            <main>
                                <p>Lado B: <input type="number" 
                                value={this.state.LadoB}
                                onChange={this.AlterarLadoB}
                                /></p>
                            </main>

                            <button onClick={this.Calcular}>Calcular</button>
                            <main>
                                <p>Resultado....({this.state.Resultado})</p>
                            </main>
                        </div>
                    </main>
                </div>
            </div>
        )
    }
}

export default Pitágoras;