import React from 'react';
import {Link} from 'react-router-dom'
import './Styles/Body1Style.css'

class Fórmulas extends React.Component{
    render(){
        return(
            <div>
                <div className='Cálculos'>
                    <main className='Título'>
                        <h2>Fórmulas Disponíveis</h2>
                    </main>
                    <main className='Seleção'>
                        <div>
                            <p><Link to='/Bashkara'>Delta</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Segundo-Grau'>Bashkara</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Pitágoras'>Pitágoras</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Aulas'>Como Fazer</Link></p>
                        </div>
                    </main>
                    <main className='Fórmulator'>         
                        <h3>Escolha uma fómula</h3>              
                    </main>
                </div>
            </div>
        )
    }
}

export default Fórmulas;