import React from 'react'
import Header from './Header/Header'
import Formulas from './Body1/Fórmulas'
import Functions from './Body2/Funcionalidades'
import Footer from './Footer/Rodapé'

import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'

import Bashkara from './Cálculos/BlocosDeCálculos/Bashkara'
import SegundoGrau from './Cálculos/BlocosDeCálculos/SegundoGrau'
import Pitágoras from './Cálculos/BlocosDeCálculos/Pitágoras'
import Aulas from './Cálculos/Aulas'

class Body extends React.Component{
    render(){
        return(
            <div>

                    <main>
                        <Router>
                            <Switch>
                                <Route path='/' exact>
                                    <Header/>
                                    <Formulas/>
                                    <Functions/>
                                    <Footer/>
                                </Route>
                                <Route path='/Bashkara'>
                                    <Header/>
                                    <Bashkara/>
                                    <Functions/>
                                    <Footer/>
                                </Route>
                                <Route path='/Segundo-Grau'>
                                    <Header/>
                                    <SegundoGrau/>
                                    <Functions/>
                                    <Footer/>
                                </Route>
                                <Route path='/Pitágoras'>
                                    <Header/>
                                    <Pitágoras/>
                                    <Functions/>
                                    <Footer/>
                                </Route>
                                <Route path='/Aulas'>
                                    <Header/>
                                    <Aulas/>
                                    <Functions/>
                                    <Footer/>
                                </Route>
                            </Switch>
                        </Router>
                    </main>

            </div>
        )
    }
}

export default Body