import React from 'react';
import {Link} from 'react-router-dom';

class Aulas extends React.Component{
    render(){
        return(
            <div>
                                <div className='Cálculos'>
                    <main className='Título'>
                        <h2>Fórmulas Disponíveis</h2>
                    </main>
                    <main className='Seleção'>
                        <div>
                            <p><Link to='/Bashkara'>Delta</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Segundo-Grau'>Bashkara</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Pitágoras'>Pitágoras</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Aulas'>Como Fazer</Link></p>
                        </div>
                    </main>
                    <main className='Fórmulator'>
                        <div className='Inputs'>
                            <h3>Link das aulas</h3>
                                <ul>
                                    <li><a href="https://youtu.be/IHyeqc5SeXM_blank" target='_blank'>Bashkara</a></li>
                                    <li><a href="https://youtu.be/PGPRh4JBIsg" target='_blank'>Pitágoras</a></li>
                                </ul>
                        </div>
                    </main>
                </div>
            </div>
        )
    }
}

export default Aulas;