import React from 'react';
import './Styles/StylesBody2.css'

class Functions extends React.Component{
    render(){
        return(
            <div>
                <main className='Functions'>
                    <main className='Título'>
                        <h2>Funcionalidades</h2>
                    </main>
                    <main className='TextBlock'>
                        <div>
                            <h3>Como usar ?</h3>
                            <p>Selecione uma fórmula disponível e preencha os dados necessários pressione em calcular e já terá o resultado.</p>
                        </div>
                        <div>
                            <h3>Novas Fórmulas ?</h3>
                            <p>Caso queira adicionar uma nova fórmula ao nosso inventário mande uma sugestão pelos contatos</p>
                        </div>
                        <div>
                            <h3>Como foi feito</h3>
                            <p>A tecnologia principal na criação do formulator foi o ReactJS que permite uma melhor expeiência de usuários em dispositivos desktop. Mas ainda funciona em dispositivos mobile</p>
                        </div>
                    </main>
                </main>
            </div>
        )
    }
}

export default Functions;