import React from 'react';
import {Link} from 'react-router-dom'
import './Styles/BDC.css'

class Bashkara extends React.Component{

    constructor(){
        super()
        this.state = {
            ValorA:'',
            ValorB:'',
            ValorC:'',
            Resultado:''
        }
    }

    AlterarValorA = event =>{
        this.setState({ValorA: event.target.value})
    }
    AlterarValorB = event =>{
        this.setState({ValorB: event.target.value})
    }
    AlterarValorC = event =>{
        this.setState({ValorC: event.target.value})
    }

    Calcular = ()=> {
        let ValorA = this.state.ValorA
        let ValorB = this.state.ValorB
        let ValorC = this.state.ValorC
        let Res = this.state.Resultado

        let A = Number(ValorA)
        let B = Number(ValorB)
        let C = Number(ValorC)

        let Delta = (B*B)-(4*A*C)

        if(Delta > 0){
            Delta = `Raiz exata ${Delta}`
        }else if (Delta < 0){
            Delta = `Raiz negativa ${Delta} impossível contar`
        }else{
            Delta = 'Raiz nula igual a 0'
        }
        
        this.setState({Resultado: Delta})

    }


    render(){

        return(
            <div>
                <div className='Cálculos'>
                    <main className='Título'>
                        <h2>Fórmulas Disponíveis</h2>
                    </main>
                    <main className='Seleção'>
                        <div>
                            <p><Link to='/Bashkara'>Delta</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Segundo-Grau'>Bashkara</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Pitágoras'>Pitágoras</Link></p>
                        </div>
                        <div>
                            <p><Link to='/Aulas'>Como Fazer</Link></p>
                        </div>
                    </main>
                    <main className='Fórmulator'>
                        <div className='Inputs'>
                            <h3>Insira os Valores</h3>
                            <main>
                                <p>Valor A: <input type="number" 
                                value={this.state.ValorA}
                                onChange={this.AlterarValorA}
                                /></p>
                                
                            </main>
                            <main>
                                <p>Valor B: <input type="number" 
                                value={this.state.ValorB}
                                onChange={this.AlterarValorB}                                
                                /></p>
                                
                            </main>
                            <main>
                                <p>Valor C: <input type="number" 
                                value={this.state.ValorC}
                                onChange={this.AlterarValorC}
                                /></p>
                                
                            </main>
                            <button onClick={this.Calcular}>Calcular</button>
                            <main>
                                <p>Resultado....({this.state.Resultado})</p>
                            </main>
                        </div>
                    </main>
                </div>
            </div>
        )
    }
}

export default Bashkara;